import * as types from '../actionTypes'; //* ---------------------------------------------------------------- *//
//* ------------- AWS ACTIONS -------------------------------------- *//
//* ---------------------------------------------------------------- *//

export var setCredentialStatusTrue = function setCredentialStatusTrue() {
  return {
    type: types.SET_CREDENTIAL_STATUS_TO_TRUE
  };
};
export var setCredentialStatusFalse = function setCredentialStatusFalse() {
  return {
    type: types.SET_CREDENTIAL_STATUS_TO_FALSE
  };
};
export var setCheckCredentialsTrue = function setCheckCredentialsTrue() {
  return {
    type: types.CHECK_CREDENTIALS_TRUE
  };
}; //* ---------------------------------------------------------------- *//
//* ------------- NAVBAR ACTIONS ----------------------------------- *//
//* ---------------------------------------------------------------- *//

export var displayCreateMenuButton = function displayCreateMenuButton() {
  return {
    type: types.SHOW_CREATE_MENU_BUTTON
  };
};
export var hideCreateMenuButton = function hideCreateMenuButton() {
  return {
    type: types.HIDE_CREATE_MENU_BUTTON
  };
};
export var toggleCreateMenuDropdown = function toggleCreateMenuDropdown(bool) {
  return {
    type: types.TOGGLE_CREATE_MENU_DROPDOWN,
    payload: bool
  };
};
export var hideCreateMenuDropdown = function hideCreateMenuDropdown() {
  return {
    type: types.HIDE_CREATE_MENU_DROPDOWN
  };
};
export var toggleCreateMenuFormItem = function toggleCreateMenuFormItem(bool) {
  return {
    type: types.TOGGLE_CREATE_MENU_FORM_ITEM,
    payload: bool
  };
};
export var menuItemToShow = function menuItemToShow(menuItem) {
  return {
    type: types.MENU_FORM_ITEM_TO_SHOW,
    payload: menuItem
  };
};
export var displayClusterInfo = function displayClusterInfo() {
  return {
    type: types.DISPLAY_CLUSTER_INFO
  };
};
export var hideClusterInfo = function hideClusterInfo() {
  return {
    type: types.HIDE_CLUSTER_INFO
  };
};
export var updateClusterData = function updateClusterData(clusterData) {
  return {
    type: types.UPDATE_CLUSTER_DATA,
    payload: clusterData
  };
};